  margin-top: 40px;
  margin-bottom: 20px;
  margin-left: 60px;
  padding: 10px;
  font-size: 20px;
}

#search-box:focus, #search-box:target {
  box-shadow: 5px 5px 2px 2px rgba(0, 0, 0, .3);
}

.country-list, .country-info {
  margin-left: 20px;
  font-size: 24px;
  line-height: 1;
  list-style: none;
}

