* {
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
        Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
    background-color: #00ccff;
}

#search-box {
    font: inherit;
    padding: 10px;
    max-width: 320px;
    margin-bottom: 20px;
    font-size: 20px;
    margin-left: 60px;
    margin-top: 40px;
}

#search-box:focus,
#search-box:target {
    box-shadow: 5px 5px 2px 2px rgba(0, 0, 0, 0.3);
}

.country-list,
.country-info {
    list-style: none;
    margin-left: 20px;
    font-size: 24px;
    line-height: 1;
}

.country-list li {
    display: flex;
    padding-bottom: 20px;
    font-size: 32px;
    font-weight: 700;
    align-items: center;
    animation: animation 1000ms;
}

@keyframes animation {
    0% {
        transform: scale(0.9);
    }

    100% {
        transform: scale(1);
    }
}

section {
    animation: animation 250ms;
}

.country-list img,
.country-info img {
    margin-right: 30px;
    /* ramka do flagi */
    /*border: solid 2px;*/
}

.country-info h1 {
    display: flex;
    align-items: center;
    margin-left: 40px;
}

.country-info span {
    font-weight: 700;
    margin-left: 40px;
}